body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body,
html {
  font-family: "Fira Sans", sans-serif;
  height: 100%;
}

.wrapper {
  position: relative;
}

.bg {
  position: absolute;
  top: 0;
  left: 0;
  background-image: url(/intern-project/static/media/bg.d212e7b7.png);
  height: 100vh;
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: -99;
}

.content {
  text-align: center;
}

.amount {
  color: #3db1ef;
  font-weight: bold;
  font-size: 4em;
}

#header {
  font-weight: bold;
  font-size: 4em;
  color: #15395d;
}

.plus {
  color: #15395d;
}

.info {
  color: #15395d;
  font-size: 1.5em;
}

.case {
  padding-top: 20vh;
}

.content-number {
  margin-top: 8vh;
}

.nav-link {
  margin-right: 10px;
  margin-left: 10px;
  color: black !important;
}

@media only screen and (max-width: 1136px) {
  .nav-link {
    margin-left: 0px;
    margin-right: 0px;
  }
}

.nav-link #login {
  margin-right: 0;
}

.header-brand {
  margin-top: 2.5vh;
  margin-bottom: 2.5vh;
  margin-left: 2vw;
}

#try-button {
  font-weight: bold;
  padding-top: 12px;
  padding-left: 25px;
  padding-right: 25px;
  padding-bottom: 12px;
  margin-right: 3vw;
}

#login {
  border-left: 10px !important;
  border-color: grey;
  font-weight: bold;
}

.vl {
  border-left: 1px solid rgb(202, 209, 208);
}

